import React from 'react';
import PropTypes from 'prop-types';
import { Select } from 'semantic-ui-react';

class Start extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      displayType: 'initial',
      lengthToShow: 1,
      goodsToDisplay: [],
    };
  }

  componentDidMount() {
    this.switcher();
  }

  switcher(whatsInDisplay) {
    let goodsForSwitcher;

    switch (whatsInDisplay) {
      case 'initial':
        goodsForSwitcher = [...this.props.goods];
        break;
      case 'reverse':
        goodsForSwitcher = [...this.props.goods].reverse();
        break;
      case 'alphabetical':
        goodsForSwitcher = [...this.props.goods].sort((a, b) => (
          a.localeCompare(b)));
        break;
      case 'byLength':
        goodsForSwitcher = [...this.props.goods].sort((a, b) => (
          b.length - a.length));
        break;
      default:
        goodsForSwitcher = [...this.props.goods];
        break;
    }

    this.setState(prevState => ({
      ...prevState,
      goodsToDisplay: goodsForSwitcher.filter(
        (item) => {
          return item.length >= this.state.lengthToShow;
        }
      ),
    }));
  }

  render() {
    return (
      <>
        <ol>
          {this.state.goodsToDisplay.map(item => <li key={item}>{item}</li>)}
        </ol>
        <button
          type="submit"
          className="ui red button"
          onClick={() => this.switcher('reverse')}
        >
          reverse
        </button>
        <button
          type="submit"
          className="ui green button"
          onClick={() => this.switcher('initial')}
        >
          reset
        </button>
        <button
          type="submit"
          className="ui grey button"
          onClick={() => this.switcher('alphabetical')}
        >
          by alphabet
        </button>
        <button
          type="submit"
          className="ui blue button"
          onClick={() => this.switcher('byLength')}
        >
          by length
        </button>
        <Select
          placeholder="Select your country"
          options={Array.from({ length: 10 }).map((el, i) => (
            { key: i + 1, value: i + 1, text: i + 1 }
          ))}
          value={this.state.lengthToShow}
          onChange={(e, { value }) => this.setState(
            { lengthToShow: Number(value) }
          )}
        />
      </>
    );
  }
}

Start.propTypes = {
  goods: PropTypes.arrayOf(PropTypes.string).isRequired,
};

export default Start;
